import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Progress } from '@/components/ui/progress';
import { 
  CreditCard, 
  Plus, 
  Edit3, 
  Trash2, 
  Calendar,
  DollarSign,
  AlertCircle,
  CheckCircle,
  ArrowDown
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

interface CreditCard {
  id: string;
  name: string;
  bank: string;
  creditLimit: number;
  usedAmount: number;
  availableAmount: number;
  startDate: string;
  endDate: string;
  reminderDate: string;
  color: string;
}

interface CreditCardsDialogProps {
  isOpen: boolean;
  onClose: () => void;
}

const CreditCardsDialog: React.FC<CreditCardsDialogProps> = ({ isOpen, onClose }) => {
  console.log('CreditCardsDialog rendered with isOpen:', isOpen); //   
  const { toast } = useToast();
  const [creditCards, setCreditCards] = useState<CreditCard[]>([]);
  const [isAddingCard, setIsAddingCard] = useState(false);
  const [editingCard, setEditingCard] = useState<CreditCard | null>(null);
  const [newCard, setNewCard] = useState({
    name: '',
    bank: '',
    creditLimit: '',
    startDate: '',
    endDate: '',
    reminderDate: '',
    color: '#8B5CF6'
  });

  const cardColors = [
    '#8B5CF6', '#EF4444', '#10B981', '#F59E0B', 
    '#3B82F6', '#EC4899', '#6366F1', '#14B8A6'
  ];

  // Load credit cards from localStorage
  useEffect(() => {
    const savedCards = localStorage.getItem('creditCards');
    if (savedCards) {
      setCreditCards(JSON.parse(savedCards));
    }
  }, []);

  // Save credit cards to localStorage
  const saveCreditCards = (cards: CreditCard[]) => {
    localStorage.setItem('creditCards', JSON.stringify(cards));
    setCreditCards(cards);
  };

  const addCreditCard = () => {
    if (!newCard.name || !newCard.bank || !newCard.creditLimit) {
      toast({
        title: "",
        description: "    ",
        variant: "destructive",
      });
      return;
    }

    const creditLimit = parseFloat(newCard.creditLimit);
    const card: CreditCard = {
      id: Date.now().toString(),
      name: newCard.name,
      bank: newCard.bank,
      creditLimit,
      usedAmount: 0,
      availableAmount: creditLimit,
      startDate: newCard.startDate,
      endDate: newCard.endDate,
      reminderDate: newCard.reminderDate,
      color: newCard.color
    };

    const updatedCards = [...creditCards, card];
    saveCreditCards(updatedCards);
    
    setNewCard({
      name: '',
      bank: '',
      creditLimit: '',
      startDate: '',
      endDate: '',
      reminderDate: '',
      color: '#8B5CF6'
    });
    setIsAddingCard(false);

    toast({
      title: " ",
      description: "    ",
    });
  };

  const deleteCreditCard = (id: string) => {
    const updatedCards = creditCards.filter(card => card.id !== id);
    saveCreditCards(updatedCards);
    
    toast({
      title: " ",
      description: "    ",
    });
  };

  const startEdit = (card: CreditCard) => {
    setEditingCard(card);
    setNewCard({
      name: card.name,
      bank: card.bank,
      creditLimit: card.creditLimit.toString(),
      startDate: card.startDate,
      endDate: card.endDate,
      reminderDate: card.reminderDate,
      color: card.color
    });
    setIsAddingCard(true);
  };

  const saveEdit = () => {
    if (!editingCard) return;

    const creditLimit = parseFloat(newCard.creditLimit);
    const updatedCard: CreditCard = {
      ...editingCard,
      name: newCard.name,
      bank: newCard.bank,
      creditLimit,
      availableAmount: creditLimit - editingCard.usedAmount,
      startDate: newCard.startDate,
      endDate: newCard.endDate,
      reminderDate: newCard.reminderDate,
      color: newCard.color
    };

    const updatedCards = creditCards.map(card => 
      card.id === editingCard.id ? updatedCard : card
    );
    saveCreditCards(updatedCards);
    
    setEditingCard(null);
    setIsAddingCard(false);
    setNewCard({
      name: '',
      bank: '',
      creditLimit: '',
      startDate: '',
      endDate: '',
      reminderDate: '',
      color: '#8B5CF6'
    });

    toast({
      title: " ",
      description: "    ",
    });
  };

  const cancelEdit = () => {
    setEditingCard(null);
    setIsAddingCard(false);
    setNewCard({
      name: '',
      bank: '',
      creditLimit: '',
      startDate: '',
      endDate: '',
      reminderDate: '',
      color: '#8B5CF6'
    });
  };

  const makeWithdrawal = (cardId: string, amount: number) => {
    const updatedCards = creditCards.map(card => {
      if (card.id === cardId) {
        const newUsedAmount = card.usedAmount + amount;
        if (newUsedAmount > card.creditLimit) {
          toast({
            title: "",
            description: "    ",
            variant: "destructive",
          });
          return card;
        }
        return {
          ...card,
          usedAmount: newUsedAmount,
          availableAmount: card.creditLimit - newUsedAmount
        };
      }
      return card;
    });
    saveCreditCards(updatedCards);
  };

  const getUsagePercentage = (card: CreditCard) => {
    return (card.usedAmount / card.creditLimit) * 100;
  };

  const isNearDueDate = (endDate: string) => {
    if (!endDate) return false;
    const today = new Date();
    const dueDate = new Date(endDate);
    const diffTime = dueDate.getTime() - today.getTime();
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays <= 7 && diffDays >= 0;
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[95vh] overflow-y-auto p-3 sm:p-6">
        <DialogHeader className="pb-4">
          <DialogTitle className="flex items-center gap-2 text-lg sm:text-xl">
            <CreditCard className="h-5 w-5 sm:h-6 sm:w-6 text-purple-600" />
              
          </DialogTitle>
        </DialogHeader>

        <div className="space-y-4 sm:space-y-6">
          {/* Add New Card Button */}
          {!isAddingCard && (
            <Button
              onClick={() => setIsAddingCard(true)}
              className="w-full bg-purple-600 hover:bg-purple-700 text-white py-3 text-base"
            >
              <Plus className="h-4 w-4 mr-2" />
                 
            </Button>
          )}

          {/* Add/Edit Card Form */}
          {isAddingCard && (
            <div className="bg-gray-50 p-3 sm:p-4 rounded-lg border">
              <h3 className="text-base sm:text-lg font-semibold mb-3 sm:mb-4">
                {editingCard ? '  ' : '   '}
              </h3>
              
              <div className="grid grid-cols-1 gap-3 sm:gap-4">
                {/*   */}
                <div>
                  <Label htmlFor="cardName" className="text-sm font-medium"> </Label>
                  <Input
                    id="cardName"
                    value={newCard.name}
                    onChange={(e) => setNewCard({...newCard, name: e.target.value})}
                    placeholder=":  "
                    className="mt-1 h-11"
                  />
                </div>
                
                {/*   */}
                <div>
                  <Label htmlFor="bankName" className="text-sm font-medium"> </Label>
                  <Input
                    id="bankName"
                    value={newCard.bank}
                    onChange={(e) => setNewCard({...newCard, bank: e.target.value})}
                    placeholder=":  "
                    className="mt-1 h-11"
                  />
                </div>
                
                {/*   */}
                <div>
                  <Label htmlFor="creditLimit" className="text-sm font-medium">  ()</Label>
                  <Input
                    id="creditLimit"
                    type="number"
                    value={newCard.creditLimit}
                    onChange={(e) => setNewCard({...newCard, creditLimit: e.target.value})}
                    placeholder="10000"
                    className="mt-1 h-11"
                  />
                </div>
                
                {/*   */}
                <div>
                  <Label htmlFor="cardColor" className="text-sm font-medium"> </Label>
                  <div className="flex gap-3 mt-2 flex-wrap">
                    {cardColors.map(color => (
                      <button
                        key={color}
                        type="button"
                        className={`w-10 h-10 rounded-full border-2 ${
                          newCard.color === color ? 'border-gray-800' : 'border-gray-300'
                        }`}
                        style={{ backgroundColor: color }}
                        onClick={() => setNewCard({...newCard, color})}
                      />
                    ))}
                  </div>
                </div>
                
                {/*    */}
                <div>
                  <Label htmlFor="startDate" className="text-sm font-medium">  </Label>
                  <Input
                    id="startDate"
                    type="date"
                    value={newCard.startDate}
                    onChange={(e) => setNewCard({...newCard, startDate: e.target.value})}
                    className="mt-1 h-11"
                  />
                </div>
                
                {/*    */}
                <div>
                  <Label htmlFor="endDate" className="text-sm font-medium">  </Label>
                  <Input
                    id="endDate"
                    type="date"
                    value={newCard.endDate}
                    onChange={(e) => setNewCard({...newCard, endDate: e.target.value})}
                    className="mt-1 h-11"
                  />
                </div>
                
                {/*   */}
                <div>
                  <Label htmlFor="reminderDate" className="text-sm font-medium"> </Label>
                  <Input
                    id="reminderDate"
                    type="date"
                    value={newCard.reminderDate}
                    onChange={(e) => setNewCard({...newCard, reminderDate: e.target.value})}
                    className="mt-1 h-11"
                  />
                </div>
              </div>
              
              {/*    */}
              <div className="flex flex-col sm:flex-row gap-3 mt-4">
                <Button
                  onClick={editingCard ? saveEdit : addCreditCard}
                  className="bg-green-600 hover:bg-green-700 text-white py-3 text-base flex-1"
                >
                  <CheckCircle className="h-4 w-4 mr-2" />
                  {editingCard ? ' ' : ' '}
                </Button>
                <Button
                  onClick={cancelEdit}
                  variant="outline"
                  className="py-3 text-base flex-1"
                >
                  
                </Button>
              </div>
            </div>
          )}

          {/* Credit Cards List */}
          <div className="space-y-3 sm:space-y-4">
            {creditCards.length === 0 ? (
              <div className="text-center py-6 sm:py-8 text-gray-500">
                <CreditCard className="h-10 w-10 sm:h-12 sm:w-12 mx-auto mb-3 sm:mb-4 text-gray-300" />
                <p className="text-sm sm:text-base">     </p>
              </div>
            ) : (
              creditCards.map((card) => (
                <div
                  key={card.id}
                  className="bg-white border-2 rounded-lg p-3 sm:p-4 shadow-sm"
                  style={{ borderColor: card.color }}
                >
                  {/* Card Header */}
                  <div className="flex justify-between items-start mb-3">
                    <div className="flex-1">
                      <h4 className="text-base sm:text-lg font-semibold text-gray-800">{card.name}</h4>
                      <p className="text-sm text-gray-600">{card.bank}</p>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={() => startEdit(card)}
                        className="p-2 text-blue-600 hover:bg-blue-50 rounded-full transition-colors"
                      >
                        <Edit3 className="h-4 w-4" />
                      </button>
                      <button
                        onClick={() => deleteCreditCard(card.id)}
                        className="p-2 text-red-600 hover:bg-red-50 rounded-full transition-colors"
                      >
                        <Trash2 className="h-4 w-4" />
                      </button>
                    </div>
                  </div>

                  {/* Credit Limit Progress Bar */}
                  <div className="mb-3 sm:mb-4">
                    <div className="flex justify-between items-center mb-2">
                      <span className="text-sm font-medium text-gray-700"> </span>
                      <span className="text-xs sm:text-sm text-gray-600">
                        {card.availableAmount.toFixed(2)} / {card.creditLimit.toFixed(2)} .
                      </span>
                    </div>
                    <Progress 
                      value={getUsagePercentage(card)} 
                      className="h-2 sm:h-3"
                      style={{ 
                        '--progress-background': card.color,
                        '--progress-foreground': card.color 
                      } as React.CSSProperties}
                    />
                    <div className="flex justify-between text-xs text-gray-500 mt-1">
                      <span>: {card.usedAmount.toFixed(2)} .</span>
                      <span>: {card.availableAmount.toFixed(2)} .</span>
                    </div>
                  </div>

                  {/* Near Due Date Warning */}
                  {isNearDueDate(card.endDate) && (
                    <div className="flex items-center gap-2 mb-3 p-2 bg-orange-50 border border-orange-200 rounded-lg">
                      <AlertCircle className="h-4 w-4 text-orange-600 flex-shrink-0" />
                      <span className="text-sm text-orange-700">  !</span>
                    </div>
                  )}

                  {/* Dates Information */}
                  {(card.startDate || card.endDate || card.reminderDate) && (
                    <div className="grid grid-cols-1 gap-2 text-xs text-gray-600 mb-3 sm:mb-4">
                      {card.startDate && (
                        <div className="flex items-center gap-2">
                          <Calendar className="h-3 w-3 flex-shrink-0" />
                          <span>: {new Date(card.startDate).toLocaleDateString('ar-EG')}</span>
                        </div>
                      )}
                      {card.endDate && (
                        <div className="flex items-center gap-2">
                          <Calendar className="h-3 w-3 flex-shrink-0" />
                          <span>: {new Date(card.endDate).toLocaleDateString('ar-EG')}</span>
                        </div>
                      )}
                      {card.reminderDate && (
                        <div className="flex items-center gap-2">
                          <AlertCircle className="h-3 w-3 flex-shrink-0" />
                          <span>: {new Date(card.reminderDate).toLocaleDateString('ar-EG')}</span>
                        </div>
                      )}
                    </div>
                  )}

                  {/* Withdrawal Button */}
                  <Button
                    onClick={() => {
                      const amount = prompt('  :');
                      if (amount && !isNaN(parseFloat(amount))) {
                        makeWithdrawal(card.id, parseFloat(amount));
                      }
                    }}
                    className="w-full bg-red-600 hover:bg-red-700 text-white py-3 text-base"
                    disabled={card.availableAmount <= 0}
                  >
                    <ArrowDown className="h-4 w-4 mr-2" />
                      
                  </Button>
                </div>
              ))
            )}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default CreditCardsDialog;