import React from "react";
import { formatNumberEnglish } from "@/utils/formatters";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Badge } from "@/components/ui/badge";
import { Receipt, Printer, X, Trash2, Lock, Eye, EyeOff, CheckCircle, MessageCircle, Download } from "lucide-react";
import { MasterPinService } from "@/utils/masterPinService";
import { useToast } from "@/hooks/use-toast";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import { createWhatsAppLink, validatePhoneNumber, formatPhoneNumber } from "@/utils/whatsappService";
import html2canvas from "html2canvas";
import { getStorage, ref as storageRef, uploadBytes, getDownloadURL } from "firebase/storage";
import { httpsCallable } from "firebase/functions";
import { functions } from "@/firebase";

interface Transaction {
  id: string;
  type: 'transfer' | 'withdraw';
  sender_msisdn: string;
  receiver_msisdn: string;
  amount: number;
  commission: number;
  status: 'completed' | 'pending' | 'failed';
  createdAt: string;
  updatedAt: string;
  reference?: string;
  note?: string;
  sender?: string;
  receiver?: string;
  txnType?: string;
  commissionMode?: 'add' | 'deduct';
  totalCharged?: number;
  fee?: number;
}

interface ReceiptModalProps {
  isOpen: boolean;
  onClose: () => void;
  transaction: Transaction | null;
  onPrint: () => void;
  onDelete?: (transactionId: string) => void;
  onComplete?: (transactionId: string) => void;
}

const ReceiptModal: React.FC<ReceiptModalProps> = ({
  isOpen,
  onClose,
  transaction,
  onPrint,
  onDelete,
  onComplete
}) => {
  const { toast } = useToast();
  const { profile } = useFirebaseAuth();
  const userId = (profile as any)?.userId || undefined;
  const isReceiptNoteEnabled = React.useMemo(() => {
    try {
      const key = userId ? `receiptNoteEnabled_${userId}` : 'receiptNoteEnabled';
      const v = localStorage.getItem(key) ?? localStorage.getItem('receiptNoteEnabled');
      //      
      return v === null ? true : v === 'true';
    } catch {
      return true;
    }
  }, [userId]);
  const [isDeletePinDialogOpen, setIsDeletePinDialogOpen] = React.useState(false);
  const [deletePin, setDeletePin] = React.useState("");
  const [showDeletePin, setShowDeletePin] = React.useState(false);
  const [deleteError, setDeleteError] = React.useState("");
  const [isVerifying, setIsVerifying] = React.useState(false);

  const [isCompletePinDialogOpen, setIsCompletePinDialogOpen] = React.useState(false);
  const [completePin, setCompletePin] = React.useState("");
  const [showCompletePin, setShowCompletePin] = React.useState(false);
  const [completeError, setCompleteError] = React.useState("");
  const receiptRef = React.useRef<HTMLDivElement>(null);
  const [isVerifyingComplete, setIsVerifyingComplete] = React.useState(false);
  const [simLabel, setSimLabel] = React.useState<string>("");

  React.useEffect(() => {
    if (!transaction?.receiver_msisdn) return;

    const normalize = (num: string) => {
      let s = String(num || '').replace(/[^0-9]/g, '');
      if (s.startsWith('20')) s = '0' + s.slice(2);
      if (s.startsWith('0')) return s;
      if (/^(10|11|12|15)/.test(s)) return '0' + s;
      return s;
    };

    const currentUid = userId || 'default';
    const s1 = localStorage.getItem(`sim1Number_${currentUid}`) || localStorage.getItem('sim1Number');
    const s2 = localStorage.getItem(`sim2Number_${currentUid}`) || localStorage.getItem('sim2Number');
    
    const rec = normalize(transaction.receiver_msisdn);
    if (s1 && normalize(s1) === rec) {
      setSimLabel('SIM 1');
    } else if (s2 && normalize(s2) === rec) {
      setSimLabel('SIM 2');
    } else {
        setSimLabel('');
    }
  }, [transaction, userId]);

  if (!transaction) return null;

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'completed':
        return <Badge className="bg-green-100 text-green-800 border-green-200"></Badge>;
      case 'pending':
        return <Badge className="bg-yellow-100 text-yellow-800 border-yellow-200"> </Badge>;
      case 'failed':
        return <Badge className="bg-red-100 text-red-800 border-red-200"></Badge>;
      default:
        return <Badge className="bg-gray-100 text-gray-800 border-gray-200">{status}</Badge>;
    }
  };

  const getTransactionTypeText = (type: string) => {
    return type === 'transfer' ? '' : '';
  };

  const transactionTitleText = (() => {
    const base = (transaction as any)?.title ? String((transaction as any).title) : getTransactionTypeText(transaction.type);
    const p = String((transaction as any)?.provider || '').toLowerCase();
    const n = String((transaction as any)?.note || '').toLowerCase();
    const ap = p.includes('vodafone') ? 'vf-cash' : (p.includes('instapay') ? 'instapay' : '');
    const an = n.includes('vodafone') ? 'vf-cash' : (n.includes('instapay') ? 'instapay' : '');
    const prov = ap || an;
    if (base === '' && prov) return ` ${prov}`;
    return base;
  })();

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleString('ar-EG', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getTotalAmount = (t: Transaction) => {
    if ((t as any)?.totalCharged !== undefined && (t as any)?.totalCharged !== null) {
      return Number((t as any).totalCharged) || 0;
    }
    const commission = Number((t as any)?.commission || 0);
    const fee = Number((t as any)?.fee || 0);
    const amount = Number(t.amount || 0);
    const mode = (t as any)?.commissionMode;
    const type = (t as any)?.type || 'transfer';
    if (String(type) === 'withdraw') {
      return amount + commission + fee;
    }
    if (mode === 'add') return amount + commission + fee;
    return amount;
  };

  const totalAmount = getTotalAmount(transaction);

  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed':
        return '';
      case 'pending':
        return ' ';
      case 'failed':
        return '';
      default:
        return status;
    }
  };

  const handlePrint = () => {
    try {
      const statusText = getStatusText(transaction.status);
      const typeText = transactionTitleText;
      const shop = (transaction as any)?.shopName || ' ';
      const cashier = (transaction as any)?.cashierName || '';
      const ref = transaction.reference || transaction.id;
      const created = new Date(transaction.createdAt).toLocaleString('ar-EG');
      const amountEn = formatNumberEnglish(transaction.amount);
      const commissionEn = formatNumberEnglish(transaction.commission || 0);
      const feeEn = formatNumberEnglish((transaction as any)?.fee || 0);
      const totalEn = formatNumberEnglish(getTotalAmount(transaction));
      const noteText = String(transaction.note || '').trim();
      const managerPhone = String((profile as any)?.phone || '').trim();
      const isNoteEnabled = (() => {
        try {
          const key = userId ? `receiptNoteEnabled_${userId}` : 'receiptNoteEnabled';
          const v = localStorage.getItem(key) ?? localStorage.getItem('receiptNoteEnabled');
          return v === null ? true : v === 'true';
        } catch {
          return true;
        }
      })();
      const baseColor = transaction.type === 'withdraw' ? '#dc2626' : '#16a34a';

      const printContent = `
        <div class="receipt-root" style="font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; max-width: 520px; margin: 0 auto; padding: 24px; background: #fff; direction: rtl;">
          <div style="text-align: center; margin-bottom: 20px; border-bottom: 2px solid ${baseColor}; padding-bottom: 16px;">
            <h1 style="color: ${baseColor}; margin: 0 0 6px 0; font-size: 26px;"> ${typeText}</h1>
            <p style="color: #666; font-size: 14px; margin: 0;"> : ${ref}</p>
            <p style="color: #888; font-size: 13px; margin: 6px 0 0 0;">${created}</p>
          </div>

          <div style="background: #f8fafc; border-radius: 12px; padding: 16px; margin-bottom: 16px;">
            <h3 style="color: #1e293b; margin: 0 0 12px 0; font-size: 16px;"> </h3>
            <div style="display: flex; justify-content: space-between; margin-bottom: 8px;">
              <span style="font-weight: 600; color: #475569;"> :</span>
              <span style="color: #1e293b;">${shop}</span>
            </div>
            ${cashier ? `<div style="display:flex; justify-content:space-between;">
              <span style="font-weight:600; color:#475569;">:</span>
              <span style="color:#1e293b;">${cashier}</span>
            </div>` : ''}
          </div>

          <div style="background: #fff; border: 2px solid #e2e8f0; border-radius: 12px; padding: 16px;">
            <h3 style="color: #1e293b; margin: 0 0 12px 0; font-size: 16px;"> </h3>

            <div style="display:flex; justify-content:space-between; margin-bottom:8px; padding:6px 0; border-bottom:1px solid #f1f5f9;">
              <span style="font-weight:600; color:#475569;"> :</span>
              <span style="color:${baseColor}; font-weight:600;">${typeText}</span>
            </div>

            <div style="display:flex; justify-content:space-between; margin-bottom:8px; padding:6px 0; border-bottom:1px solid #f1f5f9;">
              <span style="font-weight:600; color:#475569;">:</span>
              <span style="color:${transaction.status === 'completed' ? '#16a34a' : transaction.status === 'pending' ? '#ca8a04' : '#dc2626'}; font-weight:600;">${statusText}</span>
            </div>

            <div style="display:flex; justify-content:space-between; margin-bottom:8px;">
              <span style="font-weight:600; color:#475569;">:</span>
              <span style="font-family:monospace;">${transaction.sender_msisdn}</span>
            </div>
            ${(transaction as any)?.senderName ? `<div style="display:flex; justify-content:space-between; margin-bottom:8px;">
              <span style="font-weight:600; color:#475569;"> :</span>
              <span>${(transaction as any).senderName}</span>
            </div>` : ''}
            <div style="display:flex; justify-content:space-between; margin-bottom:8px;">
              <span style="font-weight:600; color:#475569;">:</span>
              <div style="display:flex; align-items:center;">
                ${simLabel ? `<span style="background:${simLabel === 'SIM 1' ? '#eff6ff' : '#f3e8ff'}; color:${simLabel === 'SIM 1' ? '#1d4ed8' : '#7e22ce'}; padding:2px 6px; border-radius:4px; font-size:11px; margin-left:6px; border:1px solid ${simLabel === 'SIM 1' ? '#bfdbfe' : '#e9d5ff'}; font-weight:bold;">${simLabel}</span>` : ''}
                <span style="font-family:monospace;">${transaction.receiver_msisdn}</span>
              </div>
            </div>

            <div style="display:flex; justify-content:space-between; margin-bottom:8px;">
              <span style="font-weight:600; color:#475569;">:</span>
              <span style="font-weight:600;" dir="ltr"><span style="font-family:monospace;">${amountEn}</span>&nbsp;</span>
            </div>
            ${transaction.commission > 0 ? `<div style="display:flex; justify-content:space-between; margin-bottom:8px;">
              <span style="font-weight:600; color:#475569;">:</span>
              <span dir="ltr"><span style="font-family:monospace;">${commissionEn}</span>&nbsp;</span>
            </div>` : ''}
            ${((transaction as any)?.fee || 0) > 0 ? `<div style="display:flex; justify-content:space-between; margin-bottom:8px;">
              <span style="font-weight:600; color:#475569;">:</span>
              <span dir="ltr"><span style="font-family:monospace;">${feeEn}</span>&nbsp;</span>
            </div>` : ''}

            <div style="display:flex; justify-content:space-between; margin-top:10px; font-weight:700; font-size:16px;">
              <span>:</span>
              <span style="color:${baseColor};" dir="ltr"><span style="font-family:monospace;">${totalEn}</span>&nbsp;</span>
            </div>
          </div>
          ${isNoteEnabled && noteText ? `
          <div style="background:#eff6ff; border:1px solid #bfdbfe; border-radius:10px; padding:12px; margin-top:12px; display:flex; gap:8px;">
            <div style="color:${baseColor};"></div>
            <div style="color:${baseColor}; font-size:13px; word-break:break-word;">${noteText}</div>
          </div>
          ` : ''}
          <div style="margin-top: 16px; font-size: 13px; color: #475569; border-top: 1px solid #e2e8f0; padding-top: 10px; text-align: center;">
            ${managerPhone ? `
            <div>
                         : <span style="direction:ltr; unicode-bidi:embed; font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;">${managerPhone}</span>
            </div>
            ` : ''}
            <div style="margin-top:6px;">
              <a href="https://cashy-link.com" target="_blank" style="font-size: 12px; color: ${baseColor}; text-decoration: none; direction: ltr; unicode-bidi: embed; font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;">cashy-link.com</a>
            </div>
          </div>
        </div>
      `;

      const w = window.open('', '_blank');
      if (!w) return;
      w.document.write(`
        <html>
          <head>
            <title>  - ${ref}</title>
            <meta charset="UTF-8">
            <meta name="viewport" content="width=device-width, initial-scale=1">
            <style>
              body { margin: 0; padding: 20px; background: #f8fafc; }
              @media print { body { background: white; } }
              .receipt-root, .receipt-root * { color: ${baseColor} !important; }
            </style>
          </head>
          <body dir="rtl">
            ${printContent}
          </body>
        </html>
      `);
      w.document.close();
      w.print();
    } catch {}
  };

  const handleWhatsAppShare = async () => {
    try {
      //    :  ->   -> 
      const rawNumber = transaction.type === 'transfer'
        ? transaction.receiver_msisdn
        : transaction.sender_msisdn;

      //       
      const normalized = formatPhoneNumber(rawNumber);
      if (!validatePhoneNumber(normalized)) {
        toast({
          title: '   ',
          description: `: ${rawNumber}`,
          duration: 3000
        });
        return;
      }

      //   
      const refEl = receiptRef.current;
      if (!refEl) {
        toast({ title: '    ', description: '     ', duration: 3000 });
        return;
      }

      //      
      try { await (document as any).fonts?.ready; } catch {}

      //        
      const targetWidth = 1080;
      const scale = Math.max(2, Math.ceil((window.devicePixelRatio || 1)));
      const baseCanvas = await html2canvas(refEl, {
        scale,
        useCORS: true,
        allowTaint: true,
        backgroundColor: '#ffffff',
        width: refEl.scrollWidth,
        height: refEl.scrollHeight,
        scrollX: 0,
        scrollY: 0,
      });

      //    1080px      
      let finalCanvas: HTMLCanvasElement = baseCanvas;
      try {
        const ratio = baseCanvas.height / baseCanvas.width;
        const normalizedCanvas = document.createElement('canvas');
        normalizedCanvas.width = targetWidth;
        normalizedCanvas.height = Math.round(targetWidth * ratio);
        const ctx = normalizedCanvas.getContext('2d');
        if (ctx) {
          ctx.imageSmoothingEnabled = true;
          // @ts-ignore:      
          ctx.imageSmoothingQuality = 'high';
          ctx.drawImage(baseCanvas, 0, 0, normalizedCanvas.width, normalizedCanvas.height);
          finalCanvas = normalizedCanvas;
        }
      } catch {}

      const blob: Blob | null = await new Promise((resolve) => finalCanvas.toBlob(resolve, 'image/png'));
      if (!blob) {
        toast({ title: '  ', description: '     ', duration: 3000 });
        return;
      }

      //    Web Share API   (  )
      const fileName = `receipt-${transaction.reference || transaction.id}.png`;
      const file = new File([blob], fileName, { type: 'image/png' });
      if ((navigator as any).canShare && (navigator as any).canShare({ files: [file] })) {
        try {
          await (navigator as any).share({ files: [file], title: ' ', text: '  ' });
          //          
        } catch (shareErr) {
          //          
        }
      }

      //    Firebase Storage     WhatsApp Cloud API
      const storage = getStorage();
      const path = `public/receipts/${fileName}`;
      const sRef = storageRef(storage, path);
      await uploadBytes(sRef, blob, { contentType: 'image/png' });
      const url = await getDownloadURL(sRef);

      const typeText = getTransactionTypeText(transaction.type);
      const ref = transaction.reference || transaction.id;
      const total = getTotalAmount(transaction);
      const caption = ` ${typeText} - : ${total}\u00A0 - : ${ref || ''}`;

      try {
        const message = `${caption}\n${url}`;
        const waLink = createWhatsAppLink(normalized, message);
        window.open(waLink, '_blank');
      } catch (linkErr) {
        //        Cloud API
      }

      //     WhatsApp Cloud API
      try {
        const sendWhatsAppImage = httpsCallable(functions, 'sendWhatsAppImage');
        await sendWhatsAppImage({ to: normalized, imageUrl: url, caption });
        toast({ title: '     ', description: '       ', duration: 3000 });
      } catch (apiErr) {
        toast({ title: '     ', description: '      Cloud API  ', duration: 4000 });
      }
    } catch (e) {
      toast({ title: '   ', description: ' ', duration: 3000 });
    }
  };

  const handleSaveImage = async () => {
    try {
      const ref = receiptRef.current;
      if (!ref) {
        toast({ title: "  ", description: "     ", duration: 3000 });
        return;
      }
      const canvas = await html2canvas(ref, { scale: 2, useCORS: true, backgroundColor: "#ffffff" });
      const dataUrl = canvas.toDataURL("image/png");
      const fileName = `receipt-${transaction.reference || transaction.id}.png`;
      const link = document.createElement("a");
      link.href = dataUrl;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      toast({ title: "  ", description: `  : ${fileName}`, duration: 2000 });
    } catch (error) {
      toast({ title: "  ", description: "    ", duration: 3000 });
    }
  };

  return (
    <>
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-md md:max-w-sm w-full mx-auto overflow-hidden md:scale-[0.95] lg:scale-[0.9]" dir="rtl">
        <DialogHeader className="text-center">
          <DialogTitle className={`flex items-center justify-center gap-2 text-xl md:text-lg font-bold ${transaction.type === 'withdraw' ? 'text-red-600' : 'text-green-600'}`}>
            <Receipt className="h-6 w-6" />
             
          </DialogTitle>
        </DialogHeader>

        <div ref={receiptRef}>
          <Card className="border-2 border-primary/20 overflow-hidden rounded-xl">
            <CardContent className="p-6 md:p-4 space-y-4 md:space-y-3 break-words md:text-[13px]">
            {/* Header */}
            <div className="text-center space-y-2">
              <h3 className={`text-lg font-bold ${transaction.type === 'withdraw' ? 'text-red-600' : 'text-green-600'}`}>cashy link</h3>
              <p className={`text-sm ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>
                 {transactionTitleText}
              </p>
              {getStatusBadge(transaction.status)}
              {(transaction as any)?.shopName && (
                <p className={`text-sm font-semibold ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>
                  : {(transaction as any).shopName}
                </p>
              )}
              {(transaction as any)?.cashierName && (
                <p className={`text-xs ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>
                  : {(transaction as any).cashierName}
                </p>
              )}
            </div>

            <Separator />

            {/* Transaction Details */}
            <div className="space-y-3 break-words">
              <div className="flex justify-between items-center">
                <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}> :</span>
                <span className={`text-sm font-mono break-all ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">{transaction.id}</span>
              </div>

              <div className="flex justify-between items-center">
                <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                <span className={`text-sm font-mono break-all ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">{transaction.sender_msisdn}</span>
              </div>

              {(transaction as any)?.senderName && (
                <div className="flex justify-between items-center">
                  <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}> :</span>
                  <span className={`text-sm ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>{(transaction as any).senderName}</span>
                </div>
              )}

              <div className="flex justify-between items-center">
                <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                <div className="flex items-center gap-2">
                  {simLabel && (
                    <Badge variant="outline" className={`${simLabel === 'SIM 1' ? 'bg-blue-50 text-blue-700 border-blue-200' : 'bg-purple-50 text-purple-700 border-purple-200'} h-5 px-1.5 text-[10px]`}>
                      {simLabel}
                    </Badge>
                  )}
                  <span className={`text-sm font-mono break-all ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">{transaction.receiver_msisdn}</span>
                </div>
              </div>

              <div className="flex justify-between items-center">
                <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                <span className={`text-sm font-semibold ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">
                  <span className="font-mono">{formatNumberEnglish(transaction.amount)}</span>&nbsp;
                </span>
              </div>

              {transaction.commission > 0 && (
                <div className="flex justify-between items-center">
                  <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                  <span className={`text-sm ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">
                    <span className="font-mono">{formatNumberEnglish(transaction.commission || 0)}</span>&nbsp;
                  </span>
                </div>
              )}
              {((transaction as any)?.fee || 0) > 0 && (
                <div className="flex justify-between items-center">
                  <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                  <span className={`text-sm ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">
                    <span className="font-mono">{formatNumberEnglish((transaction as any).fee || 0)}</span>&nbsp;
                  </span>
                </div>
              )}

              <Separator />

              <div className="flex justify-between items-center font-bold text-lg md:text-base">
                <span>:</span>
                <span className={`${transaction.type === 'withdraw' ? 'text-red-600' : 'text-green-600'}`} dir="ltr">
                  <span className="font-mono">{formatNumberEnglish(totalAmount)}</span>&nbsp;
                </span>
              </div>

              <div className="flex justify-between items-center">
                <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                <span className={`text-sm ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>{formatDate(transaction.createdAt)}</span>
              </div>

              {transaction.reference && (
                <div className="flex justify-between items-center">
                  <span className={`text-sm font-medium ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>:</span>
                  <span className={`text-sm font-mono break-all ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`} dir="ltr">{transaction.reference}</span>
                </div>
              )}

              
            </div>
            {isReceiptNoteEnabled && String(transaction.note || '').trim() && (
              <div className={`p-3 rounded-md border flex gap-2 ${transaction.type === 'withdraw' ? 'bg-red-50 border-red-200' : 'bg-green-50 border-green-200'}`}>
                <MessageCircle className={`h-4 w-4 ${transaction.type === 'withdraw' ? 'text-red-600' : 'text-green-600'}`} />
                <div className={`text-sm break-words ${transaction.type === 'withdraw' ? 'text-red-800' : 'text-green-800'}`}>{String(transaction.note || '').trim()}</div>
              </div>
            )}
            <div className={`pt-2 mt-2 border-t text-xs text-center ${transaction.type === 'withdraw' ? 'text-red-700' : 'text-green-700'}`}>
              {String((profile as any)?.phone || '').trim() && (
                <div>
                             : <span dir="ltr" className="font-mono">{String((profile as any)?.phone || '').trim()}</span>
                </div>
              )}
              <a href="https://cashy-link.com" target="_blank" rel="noopener noreferrer" dir="ltr" className={`block mt-1 hover:underline font-mono ${transaction.type === 'withdraw' ? 'text-red-600' : 'text-green-600'}`}>cashy-link.com</a>
            </div>
          </CardContent>
        </Card>
        </div>

        {/* Action Buttons */}
        <div className="grid grid-cols-2 md:grid-cols-3 gap-2 pt-3">
          {onDelete && (
            <Button
              onClick={() => {
                //       
                setDeleteError("");
                setDeletePin("");
                setShowDeletePin(false);
                setIsDeletePinDialogOpen(true);
              }}
              variant="destructive"
              size="sm"
              aria-label=" "
              title=" "
              className="w-full h-9 px-2 justify-center"
            >
              <Trash2 className="h-4 w-4" />
            </Button>
          )}
          {onComplete && transaction.status === 'pending' && (
            <Button
              onClick={() => {
                setCompleteError("");
                setCompletePin("");
                setShowCompletePin(false);
                setIsCompletePinDialogOpen(true);
              }}
              className="w-full h-9 px-3 bg-emerald-600 hover:bg-emerald-700 text-white"
              size="sm"
            >
              <CheckCircle className="h-4 w-4 mr-1" />
               
            </Button>
          )}
          <Button
            onClick={handleWhatsAppShare}
            className="w-full h-9 px-2 bg-green-600 hover:bg-green-700 text-white justify-center"
            size="sm"
            aria-label="  "
            title="  "
          >
            <MessageCircle className="h-4 w-4" />
          </Button>
          <Button
            onClick={handlePrint}
            className="w-full h-9 px-2 bg-blue-600 hover:bg-blue-700 text-white justify-center"
            size="sm"
            aria-label=" "
            title=" "
          >
            <Printer className="h-4 w-4" />
          </Button>
          <Button
            onClick={handleSaveImage}
            variant="outline"
            size="sm"
            aria-label=" "
            title=" "
            className="w-full h-9 px-2 justify-center"
          >
            <Download className="h-4 w-4" />
          </Button>
          <Button
            onClick={onClose}
            variant="outline"
            size="sm"
            aria-label=""
            title=""
            className="w-full h-9 px-2 justify-center"
          >
            <X className="h-4 w-4" />
          </Button>
        </div>
      </DialogContent>
    </Dialog>
    {/* Delete Confirmation Dialog */}
    <Dialog open={isDeletePinDialogOpen} onOpenChange={setIsDeletePinDialogOpen}>
      <DialogContent className="sm:max-w-md" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-center justify-center">
            <Lock className="h-5 w-5 text-red-600" />
              
          </DialogTitle>
        </DialogHeader>

        <div className="text-center text-sm text-gray-600 mb-4">
                
        </div>

        <div className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="deletePin" className="text-right block">  </label>
            <div className="relative">
              <input
                id="deletePin"
                type={showDeletePin ? 'text' : 'password'}
                autoComplete="off"
                value={deletePin}
                onChange={(e) => setDeletePin(e.target.value)}
                placeholder="  "
                className="pr-10 w-full border rounded-md h-10 px-3"
                dir="ltr"
              />
              <button
                type="button"
                onClick={() => setShowDeletePin(!showDeletePin)}
                className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
              >
                {showDeletePin ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </button>
            </div>
          </div>

          {deleteError && (
            <div className="flex items-center gap-2 text-red-600 text-sm">
              {deleteError}
            </div>
          )}

          <div className="flex gap-2 pt-2">
            <Button
              type="button"
              variant="outline"
              className="flex-1"
              onClick={() => {
                setIsDeletePinDialogOpen(false);
                setDeletePin("");
                setDeleteError("");
                setShowDeletePin(false);
              }}
              disabled={isVerifying}
            >
              
            </Button>
            <Button
              type="button"
              className="flex-1 bg-red-600 hover:bg-red-700"
              disabled={isVerifying}
              onClick={async () => {
                setDeleteError("");
                setIsVerifying(true);
                try {
                  const hasPin = await MasterPinService.hasMasterPin(userId);
                  if (!hasPin) {
                    setDeleteError('      ');
                    toast({
                      title: '   ',
                      description: '         .',
                      variant: 'destructive',
                    });
                    return;
                  }

                  if (!deletePin) {
                    setDeleteError('   ');
                    return;
                  }

                  const valid = await MasterPinService.verifyMasterPin(deletePin, userId);
                  if (!valid) {
                    setDeleteError('   ');
                    toast({
                      title: '   ',
                      description: '      .',
                      variant: 'destructive',
                    });
                    return;
                  }

                  //  :  
                  try {
                    onDelete && transaction && onDelete(transaction.id);
                    setIsDeletePinDialogOpen(false);
                    setDeletePin("");
                    setDeleteError("");
                    setShowDeletePin(false);
                    onClose();
                  } catch (e) {}
                } finally {
                  setIsVerifying(false);
                }
              }}
            >
               
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>

    {/* Complete Confirmation Dialog */}
    <Dialog
      open={isCompletePinDialogOpen}
      onOpenChange={(open) => {
        if (!open) {
          setIsCompletePinDialogOpen(false);
          setCompletePin("");
          setCompleteError("");
          setShowCompletePin(false);
        } else {
          setIsCompletePinDialogOpen(true);
        }
      }}
    >
      <DialogContent className="sm:max-w-md" dir="rtl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2 text-center justify-center">
            <Lock className="h-5 w-5 text-emerald-600" />
              
          </DialogTitle>
        </DialogHeader>

        <div className="text-center text-sm text-gray-600 mb-4">
                 
        </div>

        <div className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="completePin" className="text-right block">  </label>
            <div className="relative">
              <input
                id="completePin"
                type={showCompletePin ? 'text' : 'password'}
                autoComplete="off"
                value={completePin}
                onChange={(e) => setCompletePin(e.target.value)}
                placeholder="  "
                className="pr-10 w-full border rounded-md h-10 px-3"
                dir="ltr"
              />
              <button
                type="button"
                onClick={() => setShowCompletePin(!showCompletePin)}
                className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
              >
                {showCompletePin ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </button>
            </div>
          </div>

          {completeError && (
            <div className="flex items-center gap-2 text-red-600 text-sm">
              {completeError}
            </div>
          )}

          <div className="flex gap-2 pt-2">
            <Button
              type="button"
              variant="outline"
              className="flex-1"
              onClick={() => {
                setIsCompletePinDialogOpen(false);
                setCompletePin("");
                setCompleteError("");
                setShowCompletePin(false);
              }}
              disabled={isVerifyingComplete}
            >
              
            </Button>
            <Button
              type="button"
              className="flex-1 bg-emerald-600 hover:bg-emerald-700"
              disabled={isVerifyingComplete}
              onClick={async () => {
                setCompleteError("");
                setIsVerifyingComplete(true);
                try {
                  const hasPin = await MasterPinService.hasMasterPin(userId);
                  if (!hasPin) {
                    setCompleteError('      ');
                    toast({
                      title: '   ',
                      description: '         .',
                      variant: 'destructive',
                    });
                    return;
                  }

                  if (!completePin) {
                    setCompleteError('   ');
                    return;
                  }

                  const valid = await MasterPinService.verifyMasterPin(completePin, userId);
                  if (!valid) {
                    setCompleteError('   ');
                    toast({
                      title: '   ',
                      description: '      .',
                      variant: 'destructive',
                    });
                    return;
                  }

                  //  :  
                  try {
                    onComplete && transaction && onComplete(transaction.id);
                    setIsCompletePinDialogOpen(false);
                    setCompletePin("");
                    setCompleteError("");
                    setShowCompletePin(false);
                    onClose();
                  } catch (e) {}
                } finally {
                  setIsVerifyingComplete(false);
                }
              }}
            >
               
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
    </>
  );
};

export default ReceiptModal;
